
export function banners(state=[],action){
    switch(action.type){
        case 'GOT_BANNER':
            return action.banners
        default:
            return state;
    }
}
export function songlist(state=[],action){
    switch(action.type){
        case 'GOT_RECOMMEND_LIST':
            return action.list
        default:
            return state
    }
}
export function playingsong(state={},action){
    switch(action.type){
        case 'GOT_SONG_URL':
            return {...state, url:action.url}
        case 'GOT_SONG_DETAIL':
            return {...state, detail:action.detail}
        case 'GOT_SONG_LYRIC':
            return {...state, lyric:action.data}
        default:
            return state
    }
}
export function singlelist(state={},action){
    switch(action.type){
        case 'GOT_SINGLE_LIST':
            return action.singlelist
        case 'CLEAR_SINGLE_LIST':
            return {}
        default:
            return state
    }
}
export function router(state={list:false,search:false},action){
    switch(action.type){
        case 'OPEN_SINGLE_LIST':
            return {...state, list:true}
        case 'CLOSE_SINGLE_LIST':
            return {...state, list:false}
        case 'OPEN_SEARCH':
            return {...state, search:true}
        case 'CLOSE_SEARCH':
            return {...state, search:false}
        default: 
            return state
    }
}
export function searchlist(state={},action){
    switch(action.type){
        case 'GOT_SEARCH_LIST':
            return action.list
        default:
            return state
    }
}

export function rank(state=[],action){
    switch(action.type){
        case 'GOT_RANK_LIST':
            return [...state, action.rank]
        default:
            return state
    }
}


