import React, { Component } from 'react'
import styles from './index.scss'
import Rank from './Tabs/Rank'
import Recommend from './Tabs/Recommend'
import List from './Tabs/List'
import { connect } from 'react-redux'
import StatusBar from './StatusBar'
import { getMoveDirection } from '../util'
class Find extends Component {
    constructor(props){
        super(props)
        this.state = {
            curr: 0,
        }
        this.currTab = ''
    }
    scroll(newD,cha){
        if(parseInt(this.currTab.style.top)>0){
            this.currTab.style.top = parseInt(this.currTab.style.top) + cha/3 +'px'
        }else{
            this.currTab.style.top = newD +'px'
        }
        return false
    }
    changeTab(e,oldLeft){
        let width = parseInt(window.getComputedStyle(document.body).width)
        let newLeft =  parseInt(this.tabs.style.left) + e
        if(newLeft>0){
            newLeft = parseInt(this.tabs.style.left) + e/3
        }
        let cha = oldLeft - newLeft
        this.tabs.style.left = newLeft +'px'
        if(cha/3){
            this.under.style.left = (this.state.curr*width+cha)/3 +'px'
        }
    }
    componentDidMount(){
        let startX, startY, endX, endY,startT, endT, distance,direction, oldLeft
        this.currTab = this.tabs.children[this.state.curr]
        this.container.ontouchmove = (e)=>{
            startT = new Date().getTime()
            var touch = e.targetTouches[0];
            endX = touch.pageX;
            endY = touch.pageY;

            if(!direction){
                direction = getMoveDirection(e,startX,startY)
            }
            if(direction === 'RIGHT'||direction === 'LEFT'){
                this.changeTab(endX-startX,oldLeft)
            }else{
                let cha = endY - startY
                let newD = parseInt(this.currTab.style.top) + cha    
                this.scroll(newD,cha)
            }
            distance = startY - endY
            startX = endX
            startY = endY
            e.preventDefault()
        }
        this.container.ontouchstart = e=>{
            oldLeft = parseInt(this.tabs.style.left)
            document.body.scrollTop = 0
            var touch = e.targetTouches[0];
            startX = touch.pageX;
            startY = touch.pageY;
        }
        this.container.ontouchend = e=>{
            if(!direction) return
            let nowLeft = parseInt(this.tabs.style.left)
            this.tabs.style.transitionDuration = '400ms'
            this.under.style.transitionDuration = '400ms'
            if(direction === 'LEFT'||direction==="RIGHT"){
                let width = parseInt(window.getComputedStyle(document.body).width), newLeft
                console.log(nowLeft - oldLeft, width/3)
                if( Math.abs(nowLeft - oldLeft)>(width/3) ){
                    newLeft = (nowLeft - oldLeft>0)?(oldLeft + width):(oldLeft - width)
                    console.log(newLeft)
                }else{
                    newLeft = oldLeft
                }
                if(newLeft>0){
                    newLeft = 0
                }else if(newLeft<-width*2){
                    newLeft = -width
                }
                this.tabs.style.left = newLeft +'px'
                direction = ''
                this.under.style.left = (0-newLeft)/3 +'px'
                this.under.style.width = '25px'
                this.setState({
                    curr: Math.abs(newLeft/width)
                },()=>{
                    this.currTab = this.tabs.children[this.state.curr]
                })
            }else{
                let bottom = parseInt(window.getComputedStyle(document.getElementById('app')).height)-270
                endT = new Date().getTime()
                let cha = distance/(endT-startT)
                if(cha>9999){
                    cha = 40
                }
                if(cha<-9999){
                    cha = -40
                }
                this.currTab.style.top = parseInt(this.currTab.style.top) - cha*200 + 'px'
                this.currTab.style.transitionDuration = '400ms'
                if(parseInt(this.currTab.style.top)>0){
                    setTimeout(() => {
                        this.currTab.style.top = 0
                    }, 0);
                }else if(parseInt(this.currTab.style.top)<-parseInt(window.getComputedStyle(this.currTab).height)+bottom){
                    setTimeout(() => {
                        this.currTab.style.top = -parseInt(window.getComputedStyle(this.currTab).height)+bottom +'px'
                    }, 0);
                }
            }
            
            setTimeout(()=>{
                this.tabs.style.transitionDuration = '0ms'
                this.currTab.style.transitionDuration = '0ms'
                this.under.style.transitionDuration = '0ms'
            },400)
            direction = ''
        }
    }
    _Tab(curr){
        const list = [
            { name:'推荐', key:'recommend'},
            { name:'排行', key:''},
            { name:'歌单', key:''},
        ]
        return (
            <div>
                <ul className={styles['tab']}>
                    {list.map((item,index) => (
                        <li key={index} className={curr==index?styles.active:''}>{item.name}</li>
                    ))}
                </ul>
                <span ref={i=>this.under = i} style={{
                    width:25
                }} className={styles.underline}></span>
            </div>
        )
    }
    render(){
        return [
            <main key="main" ref={i=>this.container=i} className={styles['main-container']}>
                <div ref={i=>this.tab = i} className={styles['tab-container']}>
                    {this._Tab(this.state.curr)}
                </div>
                <div ref={i=>this.tabs=i} className={styles['in-container']}
                  style={{
                      left:0
                  }}>
                    <Recommend history={this.props.history} style={{position:'relative',top:0, transitionDuration:'0ms'}} />
                    <Rank style={{position:'relative',top:0, transitionDuration:'0ms'}}/>
                    <List style={{position:'relative',top:0, transitionDuration:'0ms'}}/>
                </div>
            </main>
        ]
    }
}

const mapState2Props = state => {
    return state
}
const mapDispatch2Props = dispatch => {
    return {
    }
}
export default connect(
    mapState2Props,
    mapDispatch2Props
)(Find)


