import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import styles from './index.scss'
const list = [
    {
        name:'发现音乐',
        path:'/find'
    },
    {
        name:'我的音乐',
        path:'/mine'
    },
    {
        name:'搜索',
        path:'/search'
    },
]
class Menu extends Component {
    constructor(props){
        super(props)
    }
    render(){
        return [
            <div className={styles['bottom-wrap']}></div>,
            <ul className={styles['bottom-menu']}>
            {
                list.map((item,index) => (
                    <Link to={item.path}><li key={index}>{item.name}</li></Link>
                ))
            }
            </ul>
        ]
    }
}

export default Menu


