import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Welcome from '../containers/Welcome';
import Main from '../containers/Main';
import SingleList from '../components/Main/SingleList.js'
export default () => (
        <Switch>
            
    <Route exact path='/music/' component={Welcome} />
    <Route path='/music/index' component={Main} />
        </Switch>
)


