import { connect } from 'react-redux'
import Main from '../../components/Main'
import { initData,openSearch } from '../../actions/initData'
const mapState2Props = state => {
    return state
}
const mapDispatch2Props = dispatch => {
    return {
        initData(){
            dispatch(initData())
        },
        openSearch(){
            dispatch(openSearch())
        }
    }
}
export default connect(
    mapState2Props,
    mapDispatch2Props
)(Main)


