import React, { Component } from 'react'
import StatusBar from './StatusBar'
import styles from './index.scss'
import { connect } from 'react-redux'
import goBack from '../../static/images/goBack.svg'
import { closeSearch, search, getSong } from '../../actions/initData'
let starttime, endtime
class Search extends Component {
    constructor(props){
        super(props)
        this.state = {
            keyword:''
        }
        this.inputing = false
    }
    changeInputStatus(){
    }
    handleClick(id){
        this.props.getSong(id)
        document.getElementById('audio').play()
    }
    handleChange(e){
        starttime = new Date().getTime()
        this.inputing = false
        this.setState({
            keyword:e.target.value
        })
    }
    handleSearch(e){
        if(this.timer){
            clearTimeout(this.timer)
        }
        if(this.chinese){
            //return
        }
        this.timer = setTimeout(()=>{
            this.props.search(this.state.keyword)
        },1500)
    }
    componentDidMount(){
        this.i.focus()
    }
    render(){
        const { songs, songCount } = this.props
        return (
            <main className={styles['search-container']}>

                <StatusBar className={styles['search-title']} style={{
                    paddingLeft:40,
                    textAlign:'left',
                    zIndex:102,
                    }}
                    title={
                        <input ref={i=>this.i=i} className={styles.searchInput} value={this.state.keyword}
                            onCompositionUpdate={()=>this.chinese=true}
                            onCompositionEnd={()=>{this.chinese=false}}
                            onKeyUp={(e)=>this.handleSearch(e)}
                            onChange={(e)=>this.handleChange(e)}
                            placeholder="搜索歌曲、歌手..."
                             />
                    } >
                    <span className={styles.goBack} onClick={()=>this.props.goBack()}>
                        <img src={goBack} />
                    </span>
                </StatusBar>

                <div className={styles.container}>
                    <div>
                        <p>热门搜索</p>
                        <div></div>
                    </div>
                    <div>
                        {songs&&<ul>
                            {songs.map((item,index)=>(
                                <li key={index}>
                                    <span>{index+1}</span>
                                    <div onClick={()=>this.handleClick(item.id)}>
                                        <p className={styles.title}>{item.name}</p>
                                        <p className={styles.desc}>{item.artists[0].name}</p>
                                    </div>
                                </li>
                            ))}
                        </ul>}
                    </div>
                </div>

            </main>
        )
    }
}

const mapState2Props = state => {
    return state.searchlist
}
const mapDispatch2Props = dispatch => {
    return {
        getSong(id){
            dispatch(getSong(id))
        },
        search(keyword){
            dispatch(search(keyword))
        },
        goBack(){
            dispatch(closeSearch())
        }
    }
}
export default connect(
    mapState2Props,
    mapDispatch2Props
)(Search)


