import axios from 'axios'

export function initData(){
   return dispatch => {
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/banner',
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_BANNER',
                        banners:resp.data.banners
                    })
                }
            }
        )
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/personalized',
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_RECOMMEND_LIST',
                        list:resp.data.result
                    })
                }
            }
        )
        
   }
}

export function getSong(id){
    return dispatch => {
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/song/detail?ids='+id,
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_SONG_DETAIL',
                        detail:resp.data.songs
                    })
                }
            }
        )
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/music/url?id='+id,
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_SONG_URL',
                        url:resp.data.data
                    })
                }
            }
        )
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/lyric?id='+id,
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_SONG_LYRIC',
                        data:resp.data
                    })
                }
            }
        )
        
    }
}

export function getSingleList(id){
    return dispatch => {
        dispatch({
            type:'OPEN_SINGLE_LIST'
        })
        dispatch({
            type:'CLEAR_SINGLE_LIST'
        })
        if(typeof id != 'number'){
            dispatch({
                type:'GOT_SINGLE_LIST',
                singlelist:id
            })
            return 
        }
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/playlist/detail?id='+id,
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_SINGLE_LIST',
                        singlelist:resp.data.result
                    })
                }
            }
        )
    }
}


export function getRank(){
    return dispatch => {
        for(let i of [0,1,2,3,4,22,23]){
            axios({
                url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/top/list?idx='+i,
                method:'get',
                }).then(
                resp=>{
                    if(resp.data.code===200){
                        dispatch({
                            type:'GOT_RANK_LIST',
                            rank:resp.data
                        })
                    }
                }
            )
        }
    }
}

export function search(keyword){
    return dispatch => {
        axios({
            url:'https://bird.ioliu.cn/v1?url=http://120.79.162.149:3000/search?keywords='+keyword+'&offset=0',
            method:'get',
            }).then(
            resp=>{
                if(resp.data.code===200){
                    dispatch({
                        type:'GOT_SEARCH_LIST',
                        list:resp.data.result
                    })
                }
            }
        )
    }
}


export function closeSingleList(){
    return {
            type:'CLOSE_SINGLE_LIST'
        }
}

export function openSearch(){
    return {
        type:'OPEN_SEARCH'
    }
}
export function closeSearch(){
    return {
        type:'CLOSE_SEARCH'
    }
}


