import React, { Component } from 'react'
import welcome from '../../static/images/welcome.jpg'
import top from '../../static/images/welcome_top.jpg'
import bottom from '../../static/images/welcome_bottom.jpg'
import styles from './index.scss'
import actions from '../../actions'
class Welcome extends Component {
    constructor(props){
        super(props)
    }
    _goIndex(e){
        let _this = this
        setTimeout(()=>{
            _this.props.history.push('/music/index')
        },1500)
    }
    render(){
        return (
            <div onLoad={()=>this._goIndex()}  className={styles.welcome}>
            <img className={styles.main} src={top}></img>
            <img src={welcome}></img>
            <img src={bottom}></img>
            </div>
        )
    }
}
export default  Welcome


