import React, { Component } from 'react'
import { connect } from 'react-redux'
import { getRank, getSingleList } from '../../../actions/initData'
import styles from './index.scss'
class Rank extends Component {
    constructor(props){
        super(props)
    }
    handleClick(item){
        this.props.getList(item.playlist)
    }
    componentWillMount(){
        if(!this.props.rank.length){
            this.props.getRank()
        }
    }
    render(){
        const { rank,style } = this.props
        return (
            <div key="rank" className={styles.rankcontainer} style={style}>
                <div>
                    <ul className={styles.rank}>
                        {rank.map((item,index)=>(
                            <li key={index} onClick={()=>this.handleClick(item)}>
                                <div className={styles.cover}>
                                    <img src={item.playlist.coverImgUrl} />
                                </div>
                                <ul className={styles.song}>
                                    <li>{`1. ${item.playlist.tracks[0].name} - ${item.playlist.tracks[0].ar[0].name}`}</li>
                                    <li>{`2. ${item.playlist.tracks[1].name} - ${item.playlist.tracks[1].ar[0].name}`}</li>
                                    <li>{`3. ${item.playlist.tracks[2].name} - ${item.playlist.tracks[2].ar[0].name}`}</li>
                                </ul>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        )
    }
}

const mapState2Props = state => {
    return {rank:state.rank}
}
const mapDispatch2Props = dispatch => {
    return {
        getRank(){
            dispatch(getRank())
        },
        getList(list){
            dispatch(getSingleList(list))
        }
    }
}
export default connect(
    mapState2Props,
    mapDispatch2Props
)(Rank)


