import React, { Component } from 'react'
import styles from './Swipe.scss'
class Swipe extends Component {
    constructor(props){
        super(props)
        this.state = {
            active:0,
            offset:0
        }
    }
    _canControl(){
        let startX, startY, endX, endY, distance, oldOffset
        this.swipe.ontouchstart = (e) => {
            var touch = e.targetTouches[0];
            startX = touch.pageX;
            startY = touch.pageY;
            oldOffset = this.state.offset
            e.stopPropagation()
        }
        this.swipe.ontouchmove = (e) => {
            this.setTimer('close')

            var touch = e.targetTouches[0], offset = this.state.offset
            endX = touch.pageX;
            endY = touch.pageY;
            distance = startX - endX
            startX = endX
            startY = endY
            offset -= distance

            if(offset<=-this.props.width*this.props.list.length){
                offset = 0
            }
            this.setState({
                offset: offset
            })
            e.stopPropagation()
        }
        this.swipe.ontouchend = (e) => {
            let num = this.state.active
            if(this.state.offset - oldOffset >this.props.width/4){
                num -=1
            }else if(this.state.offset - oldOffset <-this.props.width/4){
                num+=1
            }
            if(num<0){
                num=0
            }
            if(num>this.props.list.length){
                num = this.props.list.length
            }
            console.log(num)
            this.setState({
                offset: -num*this.props.width,
                active: num
            })
            this.container.style.transitionDuration = `400ms`
            setTimeout(() => {
                this.setTimer('open')
                this.container.style.transitionDuration = `0ms`
            }, 400);
            e.stopPropagation()
        }
    }
    setTimer(type){
        if(type==='open'){
            this.timer = setInterval(()=>this.loop(),3000)
        }else{
            clearInterval(this.timer)
        }
    }
    loop(){
        let num = this.state.active, offset = this.state.offset
        offset -= this.props.width
        num += 1
        if(offset<=-this.props.width*this.props.list.length){
            offset = 0
            num = 0
        }
        this.setState({
            active:num,
            offset
        })
        this.container.style.transitionDuration = `400ms`
        setTimeout(() => {
            this.container.style.transitionDuration = `0ms`
        }, 0);
    }
    componentDidMount(){
        this.container.style.transitioProperty = 'transform',
        this.setTimer('open')
        
        this._canControl()
    }
    componentWillUnmount(){
        clearInterval(this.timer)
    }
    render(){
        const { active, offset } = this.state
        const { width,list,children } = this.props
        return (
            <div ref={i=>this.swipe = i} className={styles.wrap}>
                <div ref={i=>this.container = i} className={styles.content}
                  style={{
                      width: width*list.length,
                      transform:`translate(${offset}px,0)`
                  }}
                >
                {children}
                </div>
                <div className={styles.dot}>
                    {list.map((item,index)=>(
                        <span key={index} className={index==active?styles.active:''}></span>
                    ))}
                </div>
            </div>
        )
    }
}

export default Swipe


