import React, { Component } from 'react'
import StatusBar from './StatusBar'
import styles from './index.scss'
import { connect } from 'react-redux'
import Loading from '../Loading'
import { getSong, closeSingleList } from '../../actions/initData'
import { getMoveDirection } from '../util'
import goBack from '../../static/images/goBack.svg'
import headphones from '../../static/images/headphones.svg'
class SingleList extends Component {
    constructor(props){
        super(props)
        this.state={
            offset: 0,
            opacity:0
        }
        this.coverHeight=0
    }
    goBack(){
        this.props.goBack()
    }
    handleClick(id){
        this.props.getSong(id)
        document.getElementById('audio').play()
    }
    changeOffset(offset){
        this.setState((prevState)=>{
            let height = parseInt(window.getComputedStyle(this.container).height)-parseInt(window.getComputedStyle(document.body).height)+60
            let newOffset = prevState.offset+offset, newOpacity
            newOffset>0?newOffset=0:''
            newOffset<-height?newOffset=-height:''
            newOpacity = Math.abs(newOffset/this.coverHeight)
            newOpacity>1?newOpacity=1:''
            return {
                offset:newOffset,
                opacity:newOpacity
            }
        })
    }
    componentDidMount(){
        this.coverHeight = parseInt(window.getComputedStyle(this.cover.children[0]).height)
        let startX, startY, endX, endY,startT, endT, distance,direction
        this.container.ontouchmove = (e)=>{
            startT = new Date().getTime()
            var touch = e.targetTouches[0];
            endX = touch.pageX;
            endY = touch.pageY;

            if(!direction){
                direction = getMoveDirection(e,startX,startY)
            }
            if(direction === 'LEFT' || direction === 'RIGHT'){
            }else{
                this.changeOffset(endY-startY)
            }
            distance = endY-startY
            startX = endX
            startY = endY
            e.preventDefault()
        }
        this.container.ontouchstart = e=>{
            document.body.scrollTop = 0
            var touch = e.targetTouches[0];
            startX = touch.pageX;
            startY = touch.pageY;
        }
        this.container.ontouchend = e=>{
            if(!direction) return
            this.container.style.transitionDuration = '400ms'
            endT = new Date().getTime()
            let cha = distance/(endT-startT)
            if(cha>9999){
                cha = 10
            }
            if(cha<-9999){
                cha = -10
            }
            this.cover.style.transitionDuration = '400ms'
            this.list.style.transitionDuration = '400ms'
            this.changeOffset(cha*200)
        
            setTimeout(()=>{
                if(!this.cover) return 
                this.cover.style.transitionDuration = '0ms'
                this.list.style.transitionDuration = '0ms'
            },400)
            direction = ''
        }
    }
    
    render(){
        const { offset, opacity } = this.state
        const { list } = this.props
        console.log(list)
        return (
            <div className={styles.single} ref={i=>this.container=i}>
                <StatusBar className={styles['list-title']} style={{
                    opacity:opacity,
                    paddingLeft:40,
                    textAlign:'left'
                    }} title={list.name} />
                <span className={styles.goBack} onClick={()=>this.goBack()}>
                    <img src={goBack} />
                </span>
                <div ref={i=>this.cover=i} className={styles.cover} style={{backgroundImage:'url("'+list.coverImgUrl+'")', top: offset*0.5+'px' }}>
                    <div className={styles.filter}></div>
                    <div className={styles.text}>
                        <h2>{list.name}</h2>
                        <div>
                            <img src={headphones} />
                            <span>{list.playCount}</span>
                        </div>
                    </div>
                </div>
                <div ref={i=>this.list=i} style={{ top: -10+offset }} className={styles.list}>
                    {list.name?<div>
                    <div className={styles.head}>
                        <span>0</span>
                        <div>
                            <p className={styles.text}>播放全部</p>
                            <p>共1首</p>
                            </div>
                    </div>
                    <ul>
                        {list.tracks.map((item,index)=>(
                            <li key={index}>
                                <span>{index+1}</span>
                                <div onClick={()=>this.handleClick(item.id)}>
                                    <p className={styles.title}>{item.name}</p>
                                    <p className={styles.desc}>{item.artists?item.artists[0].name:item.ar[0].name}</p>
                                </div>
                            </li>
                        ))}
                    </ul>
                    </div>
                    :
                    <div className={styles.loadingcontainer}>
                        <Loading />
                    </div>
                    }
                </div>
            </div>
        )
    }
}

const mapState2Props = state => {
    return {list:state.singlelist}
}
const mapDispatch2Props = dispatch => {
    return {
        getSong(id){
            dispatch(getSong(id))
        },
        goBack(){
            dispatch(closeSingleList())
        }
    }
}
export default connect(
    mapState2Props,
    mapDispatch2Props
)(SingleList)


