import React, { Component } from 'react'
import styles from './index.scss'
import Swipe from '../../Tools/Swipe'
import { connect } from 'react-redux'
import { getSong,getSingleList } from '../../../actions/initData'
import headphones from '../../../static/images/headphones.svg'
const trans = (num)=>{
    num = parseInt(num)
    if(num>9999){
        return Math.floor(num/10000)+'万'
    }
    return num
}
class Recommend extends Component {
    constructor(props){
        super(props)
    }
    golist(id){
       this.props.getList(id)
    }
    handleClick(e,item){
        console.log(item)
        if(item.targetType == 1){
            this.props.getSong(item.targetId)
            document.getElementById('audio').play()
        }
    }
    componentDidMount(){
    }

    render(){
        let { songlist,banners,style } = this.props
        console.log(style)
        return (
            <div className={styles.content} style={style}>
                <div style={{position:'relative'}}>
                    <div className={styles.back}></div>
                    <Swipe ref={i=>this.swipe = i} width={parseInt(window.getComputedStyle(document.body).width)} list={banners}>
                    {banners.map((item,index)=>(
                        <div className={styles.card}>
                            <img src={item.picUrl} onClick={e=>this.handleClick(e,item)} />
                        </div>
                    ))}
                    </Swipe>
                 </div>
                 <div className={styles['recommend-list']}>
                     <h2>推荐歌单</h2>
                     <div className={styles.listcontainer}>
                         {songlist.map((item,index) => (
                             <div key={index} onClick={(e)=>this.golist(item.id)}>
                                 <img src={item.picUrl} />
                                 <div className={styles.count}><img src={headphones} />{trans(item.playCount)}</div>
                                 <div className={styles.title}>{item.name}</div>
                             </div>
                         ))}
                     </div>
                 </div>
            </div>
        )
    }
}
const mapState2Props = state => {
    return state
}
const mapDispatch2Props = dispatch => {
    return {
        getSong(id){
            dispatch(getSong(id))
        },
        getList(id){
            dispatch(getSingleList(id))
        }
    }
}
export default connect(
    mapState2Props,
    mapDispatch2Props
)(Recommend)


