import React, { Component } from 'react'
import ReactDOM from 'react-dom';
import Menu from './Menu'
import SingleList from './SingleList'
import { Switch, Route } from 'react-router-dom'
import StatusBar from './StatusBar'
import Router from './router'
import axios from 'axios'
import styles from './index.scss'
import search from '../../static/images/search.svg'
import Find from './Find'
import Player from './Player'
import Search from './Search'
import ReactCSSTransitionGroup from 'react-addons-css-transition-group'; 
class Main extends Component {
    constructor(props){
        super(props)
        this.state = {
            show:false
        }
        props.initData()
    }
    componentDidMount(){
        const {router} = this.props
        if(!this.state.show) return
    }
    render(){
        const { singlelist,router } = this.props
        const {show} = this.state
        return (
            <div key="index">
                <div className={styles['bar-container']}>
                    <StatusBar>
                        <img className={styles['search-icon']} src={search} onClick={()=>this.props.openSearch()} />
                    </StatusBar>
                </div>
                    <Router match={this.props.match}/>
                <div className={styles['play-container']}>
                    <Player song={this.props.playingsong} />
                </div>

                <ReactCSSTransitionGroup
                transitionName="router"
                transitionEnterTimeout={300}
                transitionLeaveTimeout={300}>
                    {
                        router.list
                        ?
                        <div className={styles['single-list']}>
                            <SingleList />
                        </div>
                        :
                        router.search&&
                        <div className={styles['single-list']}>
                            <Search />
                        </div>
                    }
                    
                </ReactCSSTransitionGroup>
            </div>
        )
    }
}

export default Main


